import React from 'react';
import { Box, Container, Typography, Grid, Card, CardContent, Chip } from '@mui/material';
import { motion } from 'framer-motion';

const Projects = () => {
  const projects = [
    { title: 'E-Commerce Platform', desc: 'Full-stack e-commerce solution with payment integration', tech: ['React', 'Node.js', 'MongoDB'] },
    { title: 'Mobile App', desc: 'Cross-platform mobile application for business management', tech: ['React Native', 'Firebase'] },
    { title: 'SaaS Platform', desc: 'Cloud-based software as a service solution', tech: ['Next.js', 'PostgreSQL', 'AWS'] },
    { title: 'AI Chatbot', desc: 'Intelligent customer support chatbot powered by AI', tech: ['Python', 'TensorFlow', 'Node.js'] }
  ];

  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>Our Projects</Typography>
          <Typography variant="h5" sx={{ color: '#e5e7eb' }}>Innovative Solutions We've Built</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={3}>
            {projects.map((project, idx) => (
              <Grid item xs={12} md={6} lg={4} key={idx}>
                <motion.div initial={{ opacity: 0, y: 20 }} whileInView={{ opacity: 1, y: 0 }} transition={{ delay: idx * 0.1 }}>
                  <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)', h: '100%' }}>
                    <CardContent>
                      <Typography variant="h6" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>{project.title}</Typography>
                      <Typography sx={{ color: '#cbd5e1', mb: 3 }}>{project.desc}</Typography>
                      <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
                        {project.tech.map((t, i) => (
                          <Chip key={i} label={t} size="small" sx={{ backgroundColor: 'rgba(139, 92, 246, 0.2)', color: '#8b5cf6' }} />
                        ))}
                      </Box>
                    </CardContent>
                  </Card>
                </motion.div>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default Projects;
