import React from 'react';
import { Box, Container, Typography, Grid, Card, CardContent, List, ListItem, ListItemIcon, ListItemText, Button } from '@mui/material';
import { CheckCircle } from '@mui/icons-material';
import { Link } from 'react-router-dom';

const Empanelment = () => {
  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>Empanelment Program</Typography>
          <Typography variant="h5" sx={{ color: '#e5e7eb' }}>Partner with Cognix AI for Growth</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            <Grid item xs={12} md={6}>
              <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)' }}>
                <CardContent>
                  <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 3 }}>Why Join Our Network?</Typography>
                  <List>
                    {['Access to cutting-edge technology', 'Revenue sharing opportunities', 'Dedicated support team', 'Marketing assistance', 'Training and resources'].map((item, idx) => (
                      <ListItem key={idx}>
                        <ListItemIcon><CheckCircle sx={{ color: '#8b5cf6' }} /></ListItemIcon>
                        <ListItemText primary={item} primaryTypographyProps={{ sx: { color: '#cbd5e1' } }} />
                      </ListItem>
                    ))}
                  </List>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={6}>
              <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)' }}>
                <CardContent>
                  <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 3 }}>Get Started</Typography>
                  <Typography sx={{ color: '#cbd5e1', mb: 3 }}>
                    Become a part of our growing partner network. We're looking for agencies, consultants, and service providers to collaborate with.
                  </Typography>
                  <Button variant="contained" component={Link} to="/contact" sx={{ background: 'linear-gradient(135deg, #8b5cf6, #3b82f6)', color: 'white' }}>Apply Now</Button>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default Empanelment;
