import React from 'react';
import { Box, Container, Typography, Grid, Card, CardContent } from '@mui/material';
import { motion } from 'framer-motion';

const About = () => {
  const values = [
    { title: 'Innovation First', desc: 'We stay ahead with cutting-edge AI and tech trends' },
    { title: 'Client Success', desc: 'Your growth and success is our primary mission' },
    { title: 'Transparency', desc: 'Clear communication and honest reporting always' },
    { title: 'Quality', desc: 'Excellence in every project and deliverable' }
  ];

  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2, fontSize: { xs: '2rem', md: '3.5rem' } }}>About Cognix AI</Typography>
          <Typography variant="h5" sx={{ color: '#e5e7eb' }}>Pioneering AI-Driven Digital Solutions</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Typography variant="h4" sx={{ color: 'white', fontWeight: 'bold', mb: 4, textAlign: 'center' }}>Who We Are</Typography>
          <Typography sx={{ color: '#cbd5e1', fontSize: '1.1rem', mb: 4, textAlign: 'center', maxWidth: '800px', mx: 'auto' }}>
            Cognix AI is a forward-thinking technology company specializing in SEO, web development, app development, and AI solutions. We combine data-driven strategies with cutting-edge technology to deliver measurable results.
          </Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Typography variant="h4" sx={{ color: 'white', fontWeight: 'bold', mb: 6, textAlign: 'center' }}>Our Values</Typography>
          <Grid container spacing={3}>
            {values.map((value, idx) => (
              <Grid item xs={12} md={6} lg={3} key={idx}>
                <motion.div initial={{ opacity: 0 }} whileInView={{ opacity: 1 }} transition={{ delay: idx * 0.1 }}>
                  <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)', h: '100%' }}>
                    <CardContent>
                      <Typography variant="h6" sx={{ color: '#8b5cf6', fontWeight: 'bold', mb: 1 }}>{value.title}</Typography>
                      <Typography sx={{ color: '#cbd5e1' }}>{value.desc}</Typography>
                    </CardContent>
                  </Card>
                </motion.div>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default About;
