import React from 'react';
import { Box, Container, Typography } from '@mui/material';

const PrivacyPolicy = () => {
  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>Privacy Policy</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg" sx={{ maxWidth: '800px' }}>
          <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>1. Information We Collect</Typography>
          <Typography sx={{ color: '#cbd5e1', mb: 4, lineHeight: 1.8 }}>We collect information you voluntarily provide through contact forms, services requests, and website interactions including your name, email, phone number, and any messages you send.</Typography>

          <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>2. How We Use Your Information</Typography>
          <Typography sx={{ color: '#cbd5e1', mb: 4, lineHeight: 1.8 }}>We use your information to provide services, respond to inquiries, improve our website, and communicate about our services.</Typography>

          <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>3. Data Security</Typography>
          <Typography sx={{ color: '#cbd5e1', mb: 4, lineHeight: 1.8 }}>We implement industry-standard security measures to protect your personal information from unauthorized access, alteration, or disclosure.</Typography>

          <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>4. Third-Party Services</Typography>
          <Typography sx={{ color: '#cbd5e1', mb: 4, lineHeight: 1.8 }}>We may use third-party service providers to assist in operating our website and conducting our business. These providers are contractually obligated to use your information only to provide services to us.</Typography>

          <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>5. Your Rights</Typography>
          <Typography sx={{ color: '#cbd5e1', mb: 4, lineHeight: 1.8 }}>You have the right to access, correct, or delete your personal information at any time. Contact us at cognixpvtltb@gmail.com to exercise these rights.</Typography>

          <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>6. Policy Changes</Typography>
          <Typography sx={{ color: '#cbd5e1', lineHeight: 1.8 }}>We may update this privacy policy periodically. Changes will be posted on this page with an updated effective date.</Typography>
        </Container>
      </Box>
    </Box>
  );
};

export default PrivacyPolicy;
