import React from 'react';
import { Box, Container, Typography, Grid, TextField, Button, Card, CardContent, Alert } from '@mui/material';
import { Mail, Phone } from '@mui/icons-material';
import { useState } from 'react';
import axios from 'axios';

const Contact = () => {
  const [formData, setFormData] = useState({ fullName: '', email: '', phone: '', message: '' });
  const [alert, setAlert] = useState({ show: false, type: '', message: '' });

  const handleChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('http://localhost:5000/api/contact', formData);
      setAlert({ show: true, type: 'success', message: response.data.message });
      setFormData({ fullName: '', email: '', phone: '', message: '' });
    } catch (error) {
      setAlert({ show: true, type: 'error', message: 'Error sending message' });
    }
  };

  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>Get in Touch</Typography>
          <Typography variant="h5" sx={{ color: '#e5e7eb' }}>We're here to help</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            <Grid item xs={12} md={8}>
              <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)' }}>
                <CardContent sx={{ p: 4 }}>
                  <Typography variant="h4" sx={{ color: 'white', fontWeight: 'bold', mb: 3 }}>Send us a Message</Typography>
                  {alert.show && <Alert severity={alert.type} sx={{ mb: 3 }}>{alert.message}</Alert>}
                  <form onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                      <Grid item xs={12} md={6}>
                        <TextField fullWidth label="Full Name" name="fullName" value={formData.fullName} onChange={handleChange} required sx={{ '& .MuiInputBase-root': { backgroundColor: 'rgba(240,240,255,0.98)' } }} />
                      </Grid>
                      <Grid item xs={12} md={6}>
                        <TextField fullWidth label="Email" name="email" type="email" value={formData.email} onChange={handleChange} required sx={{ '& .MuiInputBase-root': { backgroundColor: 'rgba(240,240,255,0.98)' } }} />
                      </Grid>
                      <Grid item xs={12} md={6}>
                        <TextField fullWidth label="Phone" name="phone" value={formData.phone} onChange={handleChange} sx={{ '& .MuiInputBase-root': { backgroundColor: 'rgba(240,240,255,0.98)' } }} />
                      </Grid>
                      <Grid item xs={12}>
                        <TextField fullWidth label="Message" name="message" multiline rows={4} value={formData.message} onChange={handleChange} required sx={{ '& .MuiInputBase-root': { backgroundColor: 'rgba(240,240,255,0.98)' } }} />
                      </Grid>
                      <Grid item xs={12}>
                        <Button type="submit" variant="contained" sx={{ background: 'linear-gradient(135deg, #8b5cf6, #3b82f6)', color: 'white', px: 4, py: 1.5 }}>Send Message</Button>
                      </Grid>
                    </Grid>
                  </form>
                </CardContent>
              </Card>
            </Grid>

            <Grid item xs={12} md={4}>
              <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)', mb: 3 }}>
                <CardContent>
                  <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
                    <Mail sx={{ color: '#8b5cf6', fontSize: 30 }} />
                    <Box>
                      <Typography sx={{ color: 'white', fontWeight: 'bold' }}>Email</Typography>
                      <Typography sx={{ color: '#cbd5e1' }}>cognixpvtltb@gmail.com</Typography>
                    </Box>
                  </Box>
                </CardContent>
              </Card>
              <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)', mb: 3 }}>
                <CardContent>
                  <Box sx={{ display: 'flex', gap: 2 }}>
                    <Phone sx={{ color: '#8b5cf6', fontSize: 30 }} />
                    <Box>
                      <Typography sx={{ color: 'white', fontWeight: 'bold' }}>Phone</Typography>
                      <Typography sx={{ color: '#cbd5e1' }}>+91 7991955775</Typography>
                    </Box>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default Contact;
