import React from 'react';
import { Box, Container, Typography, Grid, Card, CardContent, List, ListItem, ListItemIcon, ListItemText } from '@mui/material';
import { CheckCircle } from '@mui/icons-material';
import { motion } from 'framer-motion';

const Services = () => {
  const services = [
    { title: 'SEO, AEO & GEO Services', features: ['Technical SEO audits', 'Answer Engine Optimization', 'Generative Engine Optimization'] },
    { title: 'Web Development', features: ['Responsive design', 'SEO optimization', 'Performance optimization'] },
    { title: 'App Development', features: ['Native apps', 'Cross-platform solutions', 'App store optimization'] },
    { title: 'AI & Automation', features: ['Custom chatbots', 'Automation workflows', 'Computer vision solutions'] }
  ];

  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>Our Services</Typography>
          <Typography variant="h5" sx={{ color: '#e5e7eb' }}>Comprehensive Digital Solutions</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={3}>
            {services.map((service, idx) => (
              <Grid item xs={12} md={6} key={idx}>
                <motion.div initial={{ opacity: 0, y: 20 }} whileInView={{ opacity: 1, y: 0 }} transition={{ delay: idx * 0.1 }}>
                  <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)', h: '100%' }}>
                    <CardContent>
                      <Typography variant="h5" sx={{ color: 'white', fontWeight: 'bold', mb: 2 }}>{service.title}</Typography>
                      <List>
                        {service.features.map((feature, i) => (
                          <ListItem key={i} disablePadding sx={{ mb: 1 }}>
                            <ListItemIcon>
                              <CheckCircle sx={{ color: '#8b5cf6' }} />
                            </ListItemIcon>
                            <ListItemText primary={feature} primaryTypographyProps={{ sx: { color: '#cbd5e1' } }} />
                          </ListItem>
                        ))}
                      </List>
                    </CardContent>
                  </Card>
                </motion.div>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default Services;
