import React from 'react';
import { AppBar, Toolbar, Typography, Button, Box } from '@mui/material';
import { Link } from 'react-router-dom';

const Navbar = () => {
  const navItems = [
    { label: 'Home', path: '/' },
    { label: 'About', path: '/about' },
    { label: 'Services', path: '/services' },
    { label: 'Projects', path: '/projects' },
    { label: 'Contact', path: '/contact' },
    { label: 'Testimonials', path: '/testimonials' }
  ];

  return (
    <AppBar position="fixed" sx={{ background: 'linear-gradient(135deg, #0b0b0f 0%, #14141a 100%)', boxShadow: '0 4px 20px rgba(0,0,0,0.5)' }}>
      <Toolbar>
        <Typography 
          variant="h6" 
          component={Link} 
          to="/" 
          sx={{ 
            flexGrow: 1, 
            textDecoration: 'none', 
            color: 'white',
            fontWeight: 'bold',
            fontSize: '1.5rem'
          }}
        >
          Cognix AI
        </Typography>
        <Box sx={{ display: 'flex', gap: 3 }}>
          {navItems.map((item) => (
            <Button
              key={item.path}
              component={Link}
              to={item.path}
              sx={{ color: 'white', textTransform: 'none', fontSize: '1rem', '&:hover': { color: '#8b5cf6' } }}
            >
              {item.label}
            </Button>
          ))}
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
