import React from 'react';
import { Box, Container, Typography, Grid, Card, CardContent, Rating, Avatar } from '@mui/material';
import { motion } from 'framer-motion';

const Testimonials = () => {
  const testimonials = [
    { name: 'Amit Sharma', company: 'TechNova Solutions', rating: 5, message: 'Cognix AI helped us improve our search visibility significantly. Great team!' },
    { name: 'Neha Verma', company: 'SaaSFlow', rating: 5, message: 'The AI chatbot solution streamlined our customer support operations.' },
    { name: 'Rahul Mehta', company: 'FinEdge Corp', rating: 5, message: 'Professional team with strong technical capabilities. Highly recommended!' }
  ];

  return (
    <Box sx={{ backgroundColor: '#030303', minHeight: '100vh', pt: 4 }}>
      <Box sx={{ background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)', color: 'white', py: 8, textAlign: 'center' }}>
        <Container maxWidth="lg">
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>Client Testimonials</Typography>
          <Typography variant="h5" sx={{ color: '#e5e7eb' }}>What Our Clients Say</Typography>
        </Container>
      </Box>

      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={3}>
            {testimonials.map((testimonial, idx) => (
              <Grid item xs={12} md={6} lg={4} key={idx}>
                <motion.div initial={{ opacity: 0, y: 20 }} whileInView={{ opacity: 1, y: 0 }} transition={{ delay: idx * 0.1 }}>
                  <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(139, 92, 246, 0.3)', h: '100%' }}>
                    <CardContent>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
                        <Avatar sx={{ bgcolor: '#8b5cf6' }}>{testimonial.name[0]}</Avatar>
                        <Box>
                          <Typography sx={{ color: 'white', fontWeight: 'bold' }}>{testimonial.name}</Typography>
                          <Typography sx={{ color: '#9ca3af', fontSize: '0.9rem' }}>{testimonial.company}</Typography>
                        </Box>
                      </Box>
                      <Rating value={testimonial.rating} readOnly sx={{ mb: 2 }} />
                      <Typography sx={{ color: '#cbd5e1' }}>"{testimonial.message}"</Typography>
                    </CardContent>
                  </Card>
                </motion.div>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>
    </Box>
  );
};

export default Testimonials;
